body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

/* RESET CSS BEGIN */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
    
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
/*RESET CSS END*/


/* HELPER CLASSES BEGIN */
.alignLeft {
    float: left;
}

.alignRight {
    float: right;
}

.margin-bottom-10 {
    margin-bottom: 10px;
}

.margin-bottom-20 {
    margin-bottom: 20px;
}

.margin-bottom-30 {
    margin-bottom: 30px;
}

.margin-bottom-40 {
    margin-bottom: 40px;
}

strong,
b {
    font-weight: 700;
}

em,
i {
    font-style: italic;
}

p,
ul li,
ol li {
    font-size: 17px;
    line-height: 24px;
}

/*HELPER CLASSES END*/


/* GENERAL RULES*/

@font-face {
    font-family: 'HelveticaNeue-Thin';
    src: local('HelveticaNeue-Thin.otf'), url(/static/media/HelveticaNeue-Thin.c5a5cbf4.otf) format('opentype');
}

* {
   box-sizing: border-box; 
}

body {
    font-family: 'Montserrat', sans-serif;
}

.container {
    max-width: 1100px;
    width: 100%;
    margin: 0 auto;
    position: relative;
    overflow: hidden;
    padding: 40px 20px;
}


h2 {
    font-size: 52px;
    font-weight: 700;
    text-transform: uppercase;
}

.nav li {
    display: inline-block;
}

.nav li a {
    color: white;
    text-transform: uppercase;
    text-decoration: none;
    margin-left: 30px;
    padding-bottom: 3px;
    border-bottom: 4px solid white transparent;
    transition: border-bottom 0.1s, color 0.1s;
    
} 

.nav li a:hover {
    border-bottom: 4px solid white;
    transition: border-bottom 0.1s, color 0.1s;
}

#hero h2 {
    color: white;
    margin-bottom: 40px;
    
}


.imgs {
    float: right;
    position: absolute;
    right: -135px;
}

.iphone {
    width: 85%;
    position: relative;
    margin-bottom: -240px;
}

.cta-info {
    width: 60%;
    padding-right: 150px;
    float: left;
}

.hero-para {
    font-size: 36px;
    line-height: 40px;
    font-family: 'HelveticaNeue-Thin';
    color: white;
    margin-bottom: 55px;
}

.cta-button {
    padding: 23px 35px;
    background: #282828;
    color: white;
    text-decoration: none;
    border-radius: 50px;
    transition: 0.3s;
}

.cta-button:hover {
    background: #383737;
    transition: 0.3s;
}


.phone-icon {
    vertical-align: middle;
    padding-right: 20px;
}


/* NOSE SECTION */


.download-info {
    width: 50%;
    padding: 200px 0px;
}


#nose h2 {
    margin-bottom: 40px;
    color: white;
}




.download-btn {
    padding: 15px;
    background: #d9332d;
    border-radius: 50px;
    color: white;
    text-decoration: none;
    transition: 0.3s;
    margin-left: 20px;
}

.download-btn:hover {
    background: #c33934;
    transition: 0.3s;
}




/* COPYRIGHT */

.copyright .container {
    padding: 40px;
    text-align: center;
}

.copyright {
    background-color: #d7dedc;
}

.inline {
    display: inline-block;
    font-size: 15px;
    vertical-align: middle;
}

.inline a {
    text-decoration: none;
    color: black;
}

.cr,
.me {
    margin-right: 40px;
}

.fb,
.tw {
    margin-right: 40px;
}

.cr:after,
.me:after {
    content: "";
    border: 1px solid #555555;
    margin-left: 35px;
}




/* ANIMATIONS */

.iphone-on {
    position: absolute;
    right: 175px;
    top: 112px;
    width: 54%;
}

.iphone-btn {
    position: absolute;
    right: 215px;
}

.js-wp-2 {
    opacity: 0;
}

.js-wp-2.animated {
    opacity: 1;
}



.js-wp-3 {
    opacity: 0;
    -webkit-animation-delay: 3.2s;
            animation-delay: 3.2s;
}

.js-wp-3.animated {
    opacity: 1;
}



/* MEDIA QUERIES / RESPONSIVENESS */


@media only screen and (max-width: 1120px) {
    
    .cta-info {
        padding-right: 40px;
    }
    
    .download-info {
        width: 80%;
    }
    
}


@media only screen and (max-width: 820px) {
    
    .iphone,
    .iphone-on,
    .iphone-btn {
        display: none;
    }
    
    .cta-info {
        width: 100%;
        text-align: center;
    }
    
    .p-features {
        padding: 0px 30px;
    }
    
    .col-4-app {
        width: 100%;
        float: none;
        margin-bottom: 50px;  
    }
    
    .row h3 {
        margin: 15px 0;
    }
    
    .fb,
    .tw,
    .dr {
        margin-top: 20px;
    }
    
    .navbar {
        overflow: hidden;
        position: absolute;
        right: 30px;
        top: 50px;
        display: block;
        z-index: 1000;
    }
    
    
    .container nav ul {
        height: 0px;
        overflow: hidden;
        background: #282828;
        border-radius: 5px;
    }
    
    .container nav ul.open {
        height: auto;
        clear: both;
    }
    
    
    .container nav ul li {
        float: none;
        text-align: right;
        width: auto;
        margin: 0;
        display: block;
    }
    
    .container nav ul li a {
        color: white;
        padding: 10px;
        display: block;
        margin: 0;
    }
    
    .container nav ul li a:hover {
        border: none;
    }
    
    
}


@media only screen and (max-width: 700px) {
    
    .cr:after,
    .me:after {
       
        display: none;
}
    
}



























